<template><section><h1>Entity</h1>
<p><code v-pre>vc-entity</code> 组件用于加载 Cesium 实体。 作为 <code v-pre>vc-viewer</code> 或者 <code v-pre>vc-datasource-xxx</code> 子组件使用。 通过 <code v-pre>vc-entity</code> 可以加载多种可视化对象（graphics），如点、线、面、标签、广告牌等。可以直接指定 <code v-pre>vc-entity</code> 的各个 <code v-pre>graphic</code> 属性或者用 VueCesium 提供的 <code v-pre>vc-graphics-xxx</code> 组件作为 <code v-pre>vc-entity</code> 子组件指定 <code v-pre>graphics</code> 属性。</p>
<h2>示例</h2>
<h3>加载实体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer ref="viewer" @ready="ready" @LEFT_CLICK="LEFT_CLICK" @selectedEntityChanged="selectedEntityChanged" @cameraClicked="cameraClicked" @closeClicked="closeClicked">
        <vc-entity @click="entityClick" @mousedown="entityClick" @dblclick="entityClick" ref="entity" :position="position" :billboard="billboard" :description="description" :id="id"> </vc-entity>
      </vc-viewer>
      <div ref="bubbleContainer" id="bubbleContainer" hidden>
        <button id="test">Test</button>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span>
      <span class="hljs-attr">ref</span>=<span class="hljs-string">"viewer"</span>
      <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>
      <span class="hljs-attr">@LEFT_CLICK</span>=<span class="hljs-string">"LEFT_CLICK"</span>
      <span class="hljs-attr">@selectedEntityChanged</span>=<span class="hljs-string">"selectedEntityChanged"</span>
      <span class="hljs-attr">@cameraClicked</span>=<span class="hljs-string">"cameraClicked"</span>
      <span class="hljs-attr">@closeClicked</span>=<span class="hljs-string">"closeClicked"</span>
      <span class="hljs-attr">@click</span>=<span class="hljs-string">"entityClick"</span>
      <span class="hljs-attr">@mousedown</span>=<span class="hljs-string">"entityClick"</span>
      <span class="hljs-attr">@dblclick</span>=<span class="hljs-string">"entityClick"</span>
    &gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position"</span> <span class="hljs-attr">:billboard</span>=<span class="hljs-string">"billboard"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:id</span>=<span class="hljs-string">"id"</span>&gt;</span> <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"bubbleContainer"</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"bubbleContainer"</span> <span class="hljs-attr">hidden</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"test"</span>&gt;</span>Test<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">id</span>: <span class="hljs-string">'This is a billboard'</span>,
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>,
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">108</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">100</span> },
        <span class="hljs-attr">billboard</span>: {}
      }
    },
    mounted() {
      <span class="hljs-keyword">this</span>.$refs.entity.createPromise.then(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer, cesiumObject }</span>) =&gt;</span> {
        <span class="hljs-built_in">console</span>.log(cesiumObject)
        viewer.zoomTo(cesiumObject)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.viewer = viewer
        <span class="hljs-keyword">this</span>.description = <span class="hljs-keyword">this</span>.$refs.bubbleContainer.innerHTML
        <span class="hljs-keyword">this</span>.billboard = <span class="hljs-keyword">new</span> Cesium.BillboardGraphics({
          <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>, <span class="hljs-comment">// default: undefined</span>
          <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>, <span class="hljs-comment">// default</span>
          <span class="hljs-attr">pixelOffset</span>: <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">0</span>, <span class="hljs-number">-50</span>), <span class="hljs-comment">// default: (0, 0)</span>
          <span class="hljs-attr">eyeOffset</span>: <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">0.0</span>, <span class="hljs-number">0.0</span>, <span class="hljs-number">0.0</span>), <span class="hljs-comment">// default</span>
          <span class="hljs-attr">horizontalOrigin</span>: Cesium.HorizontalOrigin.CENTER, <span class="hljs-comment">// default</span>
          <span class="hljs-attr">verticalOrigin</span>: Cesium.VerticalOrigin.BOTTOM, <span class="hljs-comment">// default: CENTER</span>
          <span class="hljs-attr">scale</span>: <span class="hljs-number">0.5</span>, <span class="hljs-comment">// default: 1.0</span>
          <span class="hljs-attr">color</span>: Cesium.Color.LIME, <span class="hljs-comment">// default: WHITE</span>
          <span class="hljs-comment">// rotation: Cesium.Math.PI_OVER_FOUR, // default: 0.0</span>
          <span class="hljs-attr">alignedAxis</span>: Cesium.Cartesian3.ZERO <span class="hljs-comment">// default</span>
        })
      },
      selectedEntityChanged(entity) {
        <span class="hljs-built_in">console</span>.log(entity)
        <span class="hljs-keyword">if</span> (entity) {
          <span class="hljs-keyword">this</span>.frame = <span class="hljs-keyword">this</span>.viewer.infoBox.frame
          <span class="hljs-keyword">this</span>.frame.contentWindow.addEventListener(<span class="hljs-string">'click'</span>, <span class="hljs-keyword">this</span>.frameClick)
        } <span class="hljs-keyword">else</span> {
          <span class="hljs-keyword">this</span>.frame &amp;&amp; <span class="hljs-keyword">this</span>.frame.contentWindow.removeEventListener(<span class="hljs-string">'click'</span>, <span class="hljs-keyword">this</span>.frameClick)
        }
      },
      frameClick(event) {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'frame clicked'</span>)
        <span class="hljs-keyword">if</span> (event.target.id === <span class="hljs-string">'test'</span>) {
          <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'test clicked'</span>)
        }
      },
      LEFT_CLICK(movement) {
        <span class="hljs-keyword">const</span> feature = <span class="hljs-keyword">this</span>.viewer.scene.pick(movement.position)
        <span class="hljs-built_in">console</span>.log(feature)
      },
      cameraClicked(e) {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'cameraClicked'</span>, e)
      },
      closeClicked(e) {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'closeClicked'</span>, e)
      },
      entityClick(e) {
        <span class="hljs-built_in">console</span>.log(e)
      },
      cameraClicked(e) {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'cameraClicked'</span>, e)
      },
      closeClicked(e) {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'closeClicked'</span>, e)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>id</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 的唯一标识符。如果没有提供，则生成 GUID。</td>
</tr>
<tr>
<td>name</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 名称，名称可不必唯一。</td>
</tr>
<tr>
<td>availability</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的可用性参数。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 entity 及其子项是否显示。</td>
</tr>
<tr>
<td>description</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 的 HTML 描述信息。</td>
</tr>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 的位置。 <strong>结构：{ lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>orientation</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 的方向。</td>
</tr>
<tr>
<td>viewFrom</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 的初始偏移量。</td>
</tr>
<tr>
<td>parent</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的父实体。</td>
</tr>
<tr>
<td>billboard</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的布告板。</td>
</tr>
<tr>
<td>box</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的盒子对象。</td>
</tr>
<tr>
<td>corridor</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的走廊对象。</td>
</tr>
<tr>
<td>cylinder</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的圆柱对象。</td>
</tr>
<tr>
<td>ellipse</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的椭圆对象。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的椭球体对象。</td>
</tr>
<tr>
<td>label</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的标签对象。</td>
</tr>
<tr>
<td>model</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的模型对象。</td>
</tr>
<tr>
<td>path</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的路径对象。</td>
</tr>
<tr>
<td>plane</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的平面对象。</td>
</tr>
<tr>
<td>point</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的点对象。</td>
</tr>
<tr>
<td>polygon</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的多边形对象。</td>
</tr>
<tr>
<td>polyline</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的折线对象。</td>
</tr>
<tr>
<td>properties</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的属性。</td>
</tr>
<tr>
<td>polylineVolume</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的多线段柱体对象。</td>
</tr>
<tr>
<td>rectangle</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的矩形对象。</td>
</tr>
<tr>
<td>wall</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> 指定 entity 关联的墙对象。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Entity.html">Entity</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该实体上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该实体上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标单击该实体时触发。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标左键双击该实体时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标移动到该实体时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          id: 'This is a billboard',
          description: 'Hello Vue Cesium',
          image: 'https://zouyaoji.top/vue-cesium/favicon.png',
          position: { lng: 108, lat: 35, height: 100 },
          billboard: {}
        }
      },
      mounted() {
        this.$refs.entity.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          console.log(cesiumObject)
          viewer.zoomTo(cesiumObject)
        })
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.viewer = viewer
          this.description = this.$refs.bubbleContainer.innerHTML
          this.billboard = new Cesium.BillboardGraphics({
            image: 'https://zouyaoji.top/vue-cesium/favicon.png', // default: undefined
            show: true, // default
            pixelOffset: new Cesium.Cartesian2(0, -50), // default: (0, 0)
            eyeOffset: new Cesium.Cartesian3(0.0, 0.0, 0.0), // default
            horizontalOrigin: Cesium.HorizontalOrigin.CENTER, // default
            verticalOrigin: Cesium.VerticalOrigin.BOTTOM, // default: CENTER
            scale: 0.5, // default: 1.0
            color: Cesium.Color.LIME, // default: WHITE
            // rotation: Cesium.Math.PI_OVER_FOUR, // default: 0.0
            alignedAxis: Cesium.Cartesian3.ZERO // default
          })
        },
        selectedEntityChanged(entity) {
          console.log(entity)
          if (entity) {
            this.frame = this.viewer.infoBox.frame
            this.frame.contentWindow.addEventListener('click', this.frameClick)
          } else {
            this.frame && this.frame.contentWindow.removeEventListener('click', this.frameClick)
          }
        },
        frameClick(event) {
          console.log('frame clicked')
          if (event.target.id === 'test') {
            console.log('test clicked')
          }
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        },
        entityClick (e) {
          console.log(e)
        },
        cameraClicked(e) {
          console.log('cameraClicked', e)
        },
        closeClicked(e) {
          console.log('closeClicked', e)
        }
      }
    }
  </script>